import React from 'react';
import {Grid, Row, Col} from 'react-bootstrap';
import {Link} from 'react-router-dom';
import Icon from 'react-fontawesome';

const Footer = () => (
    <section className="footer">
        <Grid>
            <Row className="margin-top-m">
                <Col sm={4}>
                    <ul>
                        <h5>Dot Confirmed</h5>
                        <li><Link to="/">Home</Link></li>
                        <li><Link to="/more">Learn More</Link></li>
                        <li><Link to="/about">About Us</Link></li>
                    </ul>
                </Col>
                <Col sm={4}>
                    <ul>
                        <h5>Contact Us</h5>
                        <li style={{marginBottom: "10px"}}><p>The Team:</p><a href="mailto:letschat@dotconfirmed.com">letschat@dotconfirmed.com</a></li>
                        <li><p>Personal:</p><a href="mailto:scott@dotconfirmed.com">scott@dotconfirmed.com</a></li>
                        <li><a href="mailto:kristen@dotconfirmed.com">kristen@dotconfirmed.com</a></li>
                    </ul>
                </Col>
                <Col sm={4}>
                    <ul>
                        <h5>Follow Us</h5>
                        <li>
                            <a style={{marginRight: '-15px'}} href="https://www.facebook.com/dotconfirmed/" target="_blank"><Icon name='facebook-square' size='lg'/></a>
                            <a href="https://www.instagram.com/dotconfirmed/" target="_blank"><Icon name='instagram' size='lg'/></a>
                        </li>
                    </ul>
                </Col>
            </Row>
            <Row className="margin-bottom-s">
                <Col sm={12}>
                    <p>© Dot Confirmed 2017. All rights reserved. Lululemon Athletica and their logos are trademarks of
                        Lululemon Athletica Inc. © 1998-2017 lululemon athletica. All rights reserved. Dot Confirmed is
                        not affiliated with Lululemon Athletica Inc. in any way.</p>
                </Col>
            </Row>
        </Grid>
    </section>
);

export default Footer;



