import React, {Component} from "react";
import {Link} from "react-router-dom";
import {Button, Col, Grid, Row} from "react-bootstrap";
import FormContainer from "./FormContainer";
import EmailForm from "./EmailForm";
import Card from "./Card";

import find from "../img/find.jpg";
import treat from "../img/treat.jpg";
import passion from "../img/passion.jpg";
import home from "../img/email-home.jpg";

class Home extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }

    componentDidUpdate() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div>
                <section className="splash-container margin-bottom-l home">
                    <Grid className="splash">
                        <div className="hero">
                            <h1 className="heading">
                                A lulu marketplace<br/>in your pocket
                            </h1>
                            <h4 className="subheading">
                                Buy, sell and save your favorite lululemon<br/>with the new Dot Confirmed app.
                            </h4>
                            <Button bsStyle="primary" onClick={() => this.form.scrollIntoView()}>
                                Sign up for free
                            </Button>
                        </div>
                    </Grid>
                </section>

                <section>
                    <Grid>
                        <Row className="margin-bottom-l">
                            <Col sm={8}>
                                <h2 className="mt-0 mb-0">Dot Confirmed lets you</h2>
                            </Col>
                            <Col sm={4}>
                                <div style={{marginTop: "25px"}}>
                                    <Link to="/more" className="h5">Learn More</Link>
                                </div>
                            </Col>
                        </Row>

                        <Row>
                            <Col sm={4}>
                                <Card image={find} caption="Find your lulu">
                                    <p>
                                        It’s hard balancing life and fitness! We pull the pieces you want to see, so you
                                        can reach your goals wearing the perfect lululemon.
                                    </p>
                                </Card>
                            </Col>
                            <Col sm={4}>
                                <Card image={treat} caption="Treat yourself">
                                    <p>
                                        A refreshing new way to buy the lululemon that makes you look and feel confident.
                                        It’s never been easier to get the limited edition styles you missed out on.
                                        Treat yourself, you deserve it.
                                    </p>
                                </Card>
                            </Col>
                            <Col sm={4}>
                                <Card image={passion} caption="Fund your passion">
                                    <p>
                                        Sell your lululemon to make room for more! Hosting an online shop lets you refresh
                                        your look and fund your passion. The possibilities are endless.
                                    </p>
                                </Card>
                            </Col>
                        </Row>
                    </Grid>
                </section>

                <section className="margin-bottom-l" ref={form => this.form = form}>
                    <Grid>
                        <FormContainer src={home}>
                            <Row>
                                <Col smOffset={6} sm={5}>
                                    <EmailForm
                                        legend="We're launching this summer!"
                                        help="Sign up to get a special invite when the app releases and stay in the know with our newsletter"
                                    />
                                </Col>
                            </Row>
                        </FormContainer>
                    </Grid>
                </section>
            </div>
        );
    }
}

export default Home;



